import React, { Component } from 'react';
import { Typography, Box } from '@material-ui/core';

const styles = {
    root:{
        height:'100vh',
        paddingLeft: '10vw',
        paddingRight:'10vw'
    },
    main:{
        height:'100%',
    },
    title:{
        fontFamily:'PDBlack',
        fontSize:'9vh',
        color:'#0a0a0a',
        marginBottom: 15
    },
    subtitle:{
        fontFamily:'TwReg', 
        fontSize:'3vh', 
        color:'#0a0a0a',
    }
} 

class Home extends Component {
    
    render() {
        return (
            <div style={styles.root}>
                <Box flexDirection="column" style={styles.main} display="flex" justifyContent="center" alignItems="flex-start">
                    <Typography style={styles.title}>Paolo Velarde</Typography>
                    <Typography style={styles.subtitle}>
                    Software Developer | <br class="mobile-break"></br>University of Toronto</Typography>
                </Box>
            </div>
        )
    }
}

export default Home;