import React, { Component } from 'react';
import { Typography, Box, Card, CardContent, Grid } from '@material-ui/core';

const styles = {
    root:{
        height:'100vh',
        paddingLeft: '10vw',
        paddingRight:'10vw',
        backgroundColor: '#fff'
    },
    main:{
        height:'100%',
    },
    title:{
        fontFamily:'PDBlack',
        fontSize:'9vh',
        color:'#0a0a0a',
        marginBottom: 15
    },
    subtitle:{
        fontFamily:'TwBold', 
        fontSize:'5vh', 
        color:'#0a0a0a',
        marginBottom: 15
    },
    header:{
        fontFamily:'TwReg', 
        fontSize:'2.5vh', 
        color:'#0a0a0a',
        marginBottom: 5
    },
    comp:{
        fontFamily:'TwReg', 
        fontSize:'2vh', 
        color: '#949494',
    },
    box:{
        borderColor: '#e0e0e0',
        padding: 25,
        marginTop: '1vh',
        maxWidth: 325
    }
}

class Experience extends Component {
    render() {
        return (
            <div style={styles.root}>
                <Box flexDirection="column" style={styles.main} display="flex" justifyContent="center" alignItems="flex-start">
                    {/* <Typography style={styles.title}>About me</Typography> */}
                    <Typography style={styles.subtitle}>Experience</Typography>
                    <div style={{marginTop: '0vh',}}>
                        <Grid container spacing={3}>
                            <Grid item xs={12} sm={6}>
                                <Box border={1} borderRadius="borderRadius" style={styles.box}>
                                    <Typography style={styles.header}>
                                        Software Engineering Intern
                                    </Typography>
                                    <Typography style={styles.comp}>
                                        Interac Corp. | Jan - Apr '19
                                    </Typography>
                                </Box>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Box border={1} borderRadius="borderRadius" style={styles.box}>
                                    <Typography style={styles.header}>
                                        Web Developer
                                    </Typography>
                                    <Typography style={styles.comp}>
                                        University of Toronto | Sept '19 - present
                                    </Typography>
                                </Box>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Box border={1} borderRadius="borderRadius" style={styles.box}>
                                    <Typography style={styles.header}>
                                        Executive
                                    </Typography>
                                    <Typography style={styles.comp}>
                                        CS Enrichment Club | Sept '18 - present
                                    </Typography>
                                </Box>
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Box border={1} borderRadius="borderRadius" style={styles.box}>
                                    <Typography style={styles.header}>
                                        Graphic Design Intern
                                    </Typography>
                                    <Typography style={styles.comp}>
                                        University of Toronto | May - Aug '18
                                    </Typography>
                                </Box>
                            </Grid>
                        </Grid>
                    </div>
                    <br></br>
                </Box>
            </div>
        )
    }
}

export default Experience;