import React, { Component } from 'react';
import { Typography, Box, } from '@material-ui/core';

const styles = {
    root:{
        height:'100vh',
        paddingLeft: '10vw',
        paddingRight:'10vw',
        backgroundColor: '#0a0a0a'
    },
    main:{
        height:'100%',
    },
    title:{
        fontFamily:'PDBlack',
        fontSize:'9vh',
        color:'#fff',
        marginBottom: 15
    },
    subtitle:{
        fontFamily:'TwBold', 
        fontSize:'5vh', 
        color:'#fff',
        marginBottom: 7.5
    },
    text:{
        fontFamily:'TwReg', 
        fontSize:'3vh', 
        color:'#fff',
    },
    resume:{
        textDecoration: 'none', 
        fontFamily:'TwItalic', 
        fontSize:'2vh', 
        color:'#fff',
    }
}

class About extends Component {
    render() {
        return (
            <div style={styles.root}>
                <Box flexDirection="column" style={styles.main} display="flex" justifyContent="center" alignItems="flex-start">
                    {/* <Typography style={styles.title}>About me</Typography> */}
                    <Typography style={styles.subtitle}>Welcome!</Typography>
                    <Typography style={styles.text}>
                    I'm a sophomore at University of Toronto, 
                        with 2 years of experience in software development. Seeking Winter/Summer 2020 opportunities!</Typography>
                    <br></br>
                    <a style={styles.resume} href="https://drive.google.com/file/d/13JnUzHBw5tfrxz2wN7BQ38C9w64ITvrM/view?usp=sharing" target="_blank">View resume</a>
                </Box>
            </div>
        )
    }
}

export default About;